.img-background {
  z-index: -1;
  position: absolute;
}

@media only screen and (min-width: 360px) {
  .img-background {
    width: "10%";
    z-index: -1;
    position: absolute;
  }
}

@media only screen and (min-width: 480px) {
  .img-background {
    width: 200px;
    z-index: -1;
    position: absolute;
  }
}
@media only screen and (min-width: 560px) {
  .img-background {
    width: 400px;
    z-index: -1;
    position: absolute;
  }
}
